import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

// Async thunk for fetching requests
export const fetchRequests = createAsyncThunk('leave/fetchRequests', async () => {
    const response = await fetch('/api/requests'); // Adjust URL as needed
    return response.json();
});

// Initial state
const initialState = {
    requests: JSON.parse(localStorage.getItem('request')) || [],
    report: JSON.parse(localStorage.getItem('report')) || []
};

// Create the slice
const leaveSlice = createSlice({
    name: 'leave',
    initialState,
    reducers: {
        // Add leave request
        addLeaveRequest: (state, action) => {
            state.requests.push(action.payload);
            localStorage.setItem('request', JSON.stringify(state.requests));
        },
        // Approve a request
        approveRequest: (state, action) => {
            state.requests = state.requests.filter(request => request.studentId !== action.payload.studentId);
            state.report.push({ ...action.payload, status: 'Approved' });
            localStorage.setItem('request', JSON.stringify(state.requests));
            localStorage.setItem('report', JSON.stringify(state.report));
        },
        // Reject a request
        rejectRequest: (state, action) => {
            state.requests = state.requests.filter(request => request.studentId !== action.payload.studentId);
            state.report.push({ ...action.payload, status: 'Rejected' });
            localStorage.setItem('request', JSON.stringify(state.requests));
            localStorage.setItem('report', JSON.stringify(state.report));
        }
    },
    extraReducers: (builder) => {
        builder.addCase(fetchRequests.fulfilled, (state, action) => {
            state.requests = action.payload;
            localStorage.setItem('request', JSON.stringify(state.requests));
        });
    },
});

// Export actions and reducer
export const { addLeaveRequest, approveRequest, rejectRequest } = leaveSlice.actions;
export default leaveSlice.reducer;
