import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { loginUser } from '../features/userSlice';
import 'bootstrap/dist/css/bootstrap.min.css';

const Login = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const loginError = useSelector((state) => state.user.loginError);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [emailValid, setEmailValid] = useState(null);
  const [passwordValid, setPasswordValid] = useState(null);
  const [status, setStatus] = useState('');
  const [popup, setPopup] = useState('');

  const regEmailStaff = /^admin@gmail.com$/;
  const regEmailStudent = /^user@gmail.com$/;
  const passwordPattern = /^12345678$/;

  const validateEmail = (email) => {
    if (email.match(regEmailStaff) || email.match(regEmailStudent)) {
      setEmailValid(true);
    } else {
      setEmailValid(false);
    }
  };

  const validatePassword = (password) => {
    if (password.match(passwordPattern)) {
      setPasswordValid(true);
    } else {
      setPasswordValid(false);
    }
  };

  const handleSubmit = () => {
    if (!email || !password) {
      setStatus('Fields should not be empty');
      setEmailValid(null);
      setPasswordValid(null);
      return;
    }

    validateEmail(email);
    validatePassword(password);

    if (emailValid && passwordValid) {
      dispatch(loginUser({ email, password }));
      if (!loginError) {
        setPopup('Login Successfully!');
        setStatus('');
        setTimeout(() => {
          if (email.match(regEmailStaff)) {
            navigate('/staff-dashboard');
          } else if (email.match(regEmailStudent)) {
            navigate('/student-dashboard');
          }
        }, 1000);
      } else {
        setStatus(loginError);
      }
    } else {
      setStatus('Invalid email or password');
    }
  };

  return (
    <div className="container">
      {popup && (
        <div className="popup alert alert-success" role="alert">
          {popup}
        </div>
      )}
      <div className="row justify-content-center align-items-center vh-100">
        <div className="col-md-6">
          <div className="login-form p-4 border rounded shadow-sm">
            <h1 className="text-center mb-4">Login</h1>
            {status && <p className="text-danger text-center">{status}</p>}
            <div className="form-group mb-3">
              <label htmlFor="login-user">Email</label>
              <input
                type="email"
                className={`form-control ${emailValid === false ? 'is-invalid' : emailValid === true ? 'is-valid' : ''}`}
                id="login-user"
                placeholder="Email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                onBlur={() => validateEmail(email)}
              />
              {emailValid === false && <div className="invalid-feedback">Invalid email</div>}
            </div>
            <div className="form-group mb-3">
              <label htmlFor="login-pass">Password</label>
              <input
                type="password"
                className={`form-control ${passwordValid === false ? 'is-invalid' : passwordValid === true ? 'is-valid' : ''}`}
                id="login-pass"
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                onBlur={() => validatePassword(password)}
              />
              {passwordValid === false && <div className="invalid-feedback">Invalid password</div>}
            </div>
            <div className="form-check mb-3">
              <input
                type="checkbox"
                className="form-check-input"
                id="show-password"
                onChange={() => {
                  const passField = document.getElementById('login-pass');
                  passField.type = passField.type === 'password' ? 'text' : 'password';
                }}
              />
              <label className="form-check-label" htmlFor="show-password">
                Show password
              </label>
            </div>
            <button type="button" className="btn btn-primary w-100" onClick={handleSubmit}>
              Login
            </button>
            <div className="text-center mt-3">
              <a href="#" className="text-decoration-none">Forget Password?</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
