import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import Sidebar from '../components/Sidebar';
import { Pie } from 'react-chartjs-2';
import { Chart as ChartJS, Title, Tooltip, Legend, ArcElement } from 'chart.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../styles/StaffDashboard.css';

ChartJS.register(Title, Tooltip, Legend, ArcElement);

const StaffDashboard = ({ requests }) => {
    const isRequestsValid = Array.isArray(requests);

    const approvedCount = isRequestsValid ? requests.filter(request => request.status === 'Approved').length : 0;
    const rejectedCount = isRequestsValid ? requests.filter(request => request.status === 'Rejected').length : 0;

    const data = {
        labels: ['Approved', 'Rejected'],
        datasets: [{
            data: [approvedCount, rejectedCount],
            backgroundColor: ['#270aff', '#01d416'],
            borderColor: 'black',
            borderWidth: 1,
        }],
    };

    const options = {
        responsive: true,
        plugins: {
            legend: {
                display: false,
            },
            tooltip: {
                callbacks: {
                    label: (context) => {
                        const label = context.label || '';
                        const value = context.raw;
                        return `${label}: ${value} (${((value / (approvedCount + rejectedCount)) * 100).toFixed(2)}%)`;
                    },
                },
            },
        },
        layout: {
            padding: {
                bottom: 10, 
            },
        },
    };

    return (
        <div>
            <div style={{ display: 'flex' }}>
                <Sidebar />
                <main className="flex-grow-1 p-3">
                    <Container>
                        <Row>
                            <Col>
                                <h1>Welcome, Staff name</h1>
                                <div className="slider">
                                    <div className="card mb-3">
                                        <Pie data={data} options={options} style={{width:'10'}}/>
                                        <div className="content">
                                            <h3>Leave Requests</h3>
                                            <p>
                                                <span className="color-box" style={{ borderColor: 'black', backgroundColor: '#270aff' }}></span>
                                                Approved request
                                            </p>
                                            <p>
                                                <span className="color-box" style={{ borderColor: 'black', backgroundColor: '#01d416' }}></span>
                                                Rejected Request
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </main>
            </div>
        </div>
    );
};

export default StaffDashboard;
