import React, { useState } from 'react';
import { Form, Button, Container, Row, Col, Alert } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { addLeaveRequest } from '../features/leaveSlice';
import Popup from './Popup';

const LeaveForm = () => {
    const [formData, setFormData] = useState({
        name: '',
        studentId: '',
        fromDate: '',
        toDate: '',
        reasonType: '',
        reasonDescription: ''
    });

    const [showPopup, setShowPopup] = useState(false);
    const [error, setError] = useState('');
    const dispatch = useDispatch();

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
    };

    const handleSubmit = () => {
        const { name, studentId, fromDate, toDate, reasonType, reasonDescription } = formData;
        const date = new Date().toDateString();

        if (!name || !studentId || !fromDate || !toDate) {
            setError('Fields Should not be empty');
            return;
        }

        setShowPopup(true);
        setTimeout(() => {
            setShowPopup(false);
            window.location.replace("student-dashboard.html");
        }, 1000);

        const newLeaveRequest = {
            name,
            studentId,
            fromDate,
            toDate,
            reasonType,
            reasonDescription,
            receivedDate: date
        };

        dispatch(addLeaveRequest(newLeaveRequest));
    };

    return (
        <Container className="my-5">
            <Row>
                <Col>
                    <div className="text-center mb-4">
                        <a href="student-dashboard.html">Home</a> &gt; <a href="applyleave.html">Apply Leave</a>
                    </div>
                    <Form className="bg-light p-4 rounded shadow">
                        <h1 className="text-center mb-4">Leave Application Form</h1>
                        {error && <Alert variant="danger">{error}</Alert>}
                        <Form.Group controlId="name">
                            <Form.Label>Name:</Form.Label>
                            <Form.Control
                                type="text"
                                name="name"
                                value={formData.name}
                                onChange={handleChange}
                            />
                        </Form.Group>
                        <Form.Group controlId="student-id">
                            <Form.Label>Student ID:</Form.Label>
                            <Form.Control
                                type="text"
                                name="studentId"
                                value={formData.studentId}
                                onChange={handleChange}
                            />
                        </Form.Group>
                        <Row>
                            <Col>
                                <Form.Group controlId="from-date">
                                    <Form.Label>From:</Form.Label>
                                    <Form.Control
                                        type="date"
                                        name="fromDate"
                                        value={formData.fromDate}
                                        onChange={handleChange}
                                    />
                                </Form.Group>
                            </Col>
                            <Col>
                                <Form.Group controlId="to-date">
                                    <Form.Label>To:</Form.Label>
                                    <Form.Control
                                        type="date"
                                        name="toDate"
                                        value={formData.toDate}
                                        onChange={handleChange}
                                    />
                                </Form.Group>
                            </Col>
                        </Row>
                        <Form.Group controlId="reason-type">
                            <Form.Label>Reason Type:</Form.Label>
                            <Form.Control
                                as="select"
                                name="reasonType"
                                value={formData.reasonType}
                                onChange={handleChange}
                            >
                                <option value="" disabled>Select Reason Type</option>
                                <option value="Sick Leave">Sick Leave</option>
                                <option value="Vacation">On Duty</option>
                                <option value="Personal">Personal</option>
                                <option value="Other">Other</option>
                            </Form.Control>
                        </Form.Group>
                        <Form.Group controlId="reason-description">
                            <Form.Label>Reason Description:</Form.Label>
                            <Form.Control
                                as="textarea"
                                name="reasonDescription"
                                value={formData.reasonDescription}
                                onChange={handleChange}
                            />
                        </Form.Group>
                        <Button className="w-100 mt-3" onClick={handleSubmit}>Submit</Button>
                    </Form>
                </Col>
            </Row>
            {showPopup && <Popup />}
        </Container>
    );
};

export default LeaveForm;
