// src/features/userSlice.js
import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  users: [], // This will store all users added through the UserForm
  loginError: null, // This will store any login error messages
};

const userSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    addUser: (state, action) => {
      state.users.push(action.payload);
    },
    loginUser: (state, action) => {
      const { email, password } = action.payload;
      const adminEmail = 'admin@gmail.com';
      const adminPassword = '12345678';

      if (email === adminEmail && password === adminPassword) {
        state.loginError = null; // Clear previous errors
        // Optionally: Store login status or user info here
      } else {
        state.loginError = 'Invalid email or password';
      }
    },
  },
});

export const { addUser, loginUser } = userSlice.actions;
export default userSlice.reducer;
