import React from 'react';
import { Nav } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';

const Sidebar = () => {
    return (
        <nav className="bg-dark text-light p-3" style={{ height: '100vh', width: '250px' }}>
            <Nav className="flex-column">
                <Nav.Link as={Link} to="/staff-dashboard">Home</Nav.Link>
                <Nav.Link as={Link} to="/report">Report</Nav.Link>
                <Nav.Link as={Link} to="/request-table">Received Request</Nav.Link>
                <Nav.Link as={Link} to="/user-form">Add User</Nav.Link>
                <Nav.Link as={Link} to="/profilest">Profile</Nav.Link>
                <Nav.Link as={Link} to="/login">Logout</Nav.Link>
            </Nav>
        </nav>
    );
};

export default Sidebar;
