import React, { useEffect, useState } from 'react';
import { Table, Button, Container, Row, Col, Form, Modal } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { fetchRequests, approveRequest, rejectRequest } from '../features/leaveSlice';

const ReportPage = () => {
    const dispatch = useDispatch();
    const requests = useSelector(state => state.leave.report); // Access the report from the state
    const [filteredRequests, setFilteredRequests] = useState([]);
    const [filterStatus, setFilterStatus] = useState('');
    const [selectedRequest, setSelectedRequest] = useState(null);
    const [showModal, setShowModal] = useState(false);

    useEffect(() => {
        dispatch(fetchRequests()); // Fetch requests when the component mounts
    }, [dispatch]);

    useEffect(() => {
        // Ensure unique requests by using a Set to track seen studentIds
        const seen = new Set();
        const uniqueRequests = requests.filter(request => {
            const isDuplicate = seen.has(request.studentId);
            seen.add(request.studentId);
            return !isDuplicate;
        });
        setFilteredRequests(uniqueRequests);
    }, [requests]);

    useEffect(() => {
        // Apply filter to the requests
        if (filterStatus === '') {
            setFilteredRequests(requests);
        } else {
            setFilteredRequests(requests.filter(request => request.status === filterStatus));
        }
    }, [requests, filterStatus]);

    const handleFilterChange = (e) => {
        setFilterStatus(e.target.value);
    };

    const handleEdit = (request) => {
        setSelectedRequest(request);
        setShowModal(true);
    };

    const handleStatusChange = async (newStatus) => {
        if (selectedRequest) {
            const updatedRequest = { ...selectedRequest, status: newStatus };

            // Dispatch the status change action and wait for it to complete
            try {
                if (newStatus === 'Approved') {
                    await dispatch(approveRequest(updatedRequest));
                } else if (newStatus === 'Rejected') {
                    await dispatch(rejectRequest(updatedRequest));
                }

                // Remove the updated request from local state
                setFilteredRequests(prevRequests =>
                    prevRequests.filter(request => request.studentId !== selectedRequest.studentId)
                );

                // Hide modal and clear selected request
                setShowModal(false);
                setSelectedRequest(null); // Clear selected request
            } catch (error) {
                console.error('Failed to update request status:', error);
            }
        }
    };

    const handleCancel = () => {
        setShowModal(false);
        setSelectedRequest(null); // Clear selected request
    };

    return (
        <Container fluid>
            <Row className="mb-4">
                <Col md={3}>
                    <Button variant="secondary" href="/html/staff-dashboard.html">Back</Button>
                </Col>
                <Col md={9} className="text-end">
                    <Form.Select className="mb-2" value={filterStatus} onChange={handleFilterChange}>
                        <option value="">Request Type</option>
                        <option value="Approved">Approved</option>
                        <option value="Rejected">Rejected</option>
                    </Form.Select>
                </Col>
            </Row>
            <Row>
                <Col>
                    <Table striped bordered hover>
                        <thead>
                            <tr>
                                <th>S.No</th>
                                <th>Name</th>
                                <th>Student Id</th>
                                <th>From</th>
                                <th>To</th>
                                <th>Reason</th>
                                <th>Received Date</th>
                                <th>Status</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {filteredRequests.map((request, index) => (
                                <tr key={request.studentId}>
                                    <td>{index + 1}</td>
                                    <td>{request.name}</td>
                                    <td>{request.studentId}</td>
                                    <td>{request.fromDate}</td>
                                    <td>{request.toDate}</td>
                                    <td>{request.reasonType}</td>
                                    <td>{request.receivedDate}</td>
                                    <td>{request.status}</td>
                                    <td>
                                        <Button variant="warning" onClick={() => handleEdit(request)}>Edit</Button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </Table>
                </Col>
            </Row>

            <Modal show={showModal} onHide={handleCancel}>
                <Modal.Header closeButton>
                    <Modal.Title>Edit Request</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <p>Are you sure you want to update the status of this request?</p>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="success" onClick={() => handleStatusChange('Approved')}>Approve</Button>
                    <Button variant="danger" onClick={() => handleStatusChange('Rejected')}>Reject</Button>
                    <Button variant="secondary" onClick={handleCancel}>Cancel</Button>
                </Modal.Footer>
            </Modal>
        </Container>
    );
};

export default ReportPage;
