// Profile.js
import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

const Profile = ({ profile }) => {
  return (
    <div className="container mt-5">
      <div className="row justify-content-center">
        <div className="col-md-6">
          <div className="card border-light shadow-lg" style={{ borderRadius: '15px', backgroundColor: '#ffffff' }}>
            <div className="card-footer text-center" style={{ backgroundColor: '#004d99', color: '#ffffff', borderRadius: '15px 15px 0 0', height: '70px' }}>
              <h1 className="h2">Profile Card</h1>
            </div>
            <div className="card-body text-center">
              <img
                src={profile.photo}
                className="img-fluid rounded-circle mb-4"
                alt="Profile"
                style={{ width: '150px', height: '150px', objectFit: 'cover' }}
              />
              <h4 className="card-title" style={{ color: '#004d99', marginBottom: '10px' }}>{profile.name}</h4>
              <div className="table-responsive">
                <table className="table table-borderless table-sm mx-auto" style={{ maxWidth: '70%', margin:'0 40px' }}>
                  <tbody>
                    <tr>
                      <td className="text-start" style={{ padding: '0.5rem 0' }}><strong>ID:</strong></td>
                      <td className="text-start" style={{ padding: '0.5rem 0' }}>{profile.id}</td>
                    </tr>
                    <tr>
                      <td className="text-start" style={{ padding: '0.5rem 0' }}><strong>Email:</strong></td>
                      <td className="text-start" style={{ padding: '0.5rem 0' }}>{profile.email}</td>
                    </tr>
                    <tr>
                      <td className="text-start" style={{ padding: '0.5rem 0' }}><strong>Phone:</strong></td>
                      <td className="text-start" style={{ padding: '0.5rem 0' }}>{profile.phone}</td>
                    </tr>
                    <tr>
                      <td className="text-start" style={{ padding: '0.5rem 0' }}><strong>Institution:</strong></td>
                      <td className="text-start" style={{ padding: '0.5rem 0' }}>{profile.institution}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Profile;
