import React from 'react';
import { Container, Row, Col, Nav } from 'react-bootstrap';
import { useSelector } from 'react-redux';
import { Pie } from 'react-chartjs-2';
import { Link } from 'react-router-dom';
import { Chart as ChartJS, Title, Tooltip, Legend, ArcElement } from 'chart.js';
import 'bootstrap/dist/css/bootstrap.min.css';

ChartJS.register(Title, Tooltip, Legend, ArcElement);

const StudentDashboard = () => {

    const requests = useSelector(state => state.leave.report);


    const pendingCount = requests.filter(request => request.status === 'Pending').length;
    const approvedCount = requests.filter(request => request.status === 'Approved').length;
    const rejectedCount = requests.filter(request => request.status === 'Rejected').length;

    const data = {
        labels: ['Pending', 'Approved', 'Rejected'],
        datasets: [{
            data: [pendingCount, approvedCount, rejectedCount],
            backgroundColor: ['#f3ff0a', '#0fc502', '#1900f4'],
            borderColor: 'black',
            borderWidth: 1,
        }],
    };

    // Pie chart options
    const options = {
        responsive: true,
        plugins: {
            legend: {
                position: 'bottom',
                labels: {
                    boxWidth: 10, // Adjust width of legend color box
                    padding: 20,  // Adjust padding between legend items
                },
            },
            tooltip: {
                callbacks: {
                    label: (context) => {
                        const label = context.label || '';
                        const value = context.raw;
                        const total = pendingCount + approvedCount + rejectedCount;
                        return `${label}: ${value} (${((value / total) * 100).toFixed(2)}%)`;
                    },
                },
            },
        },
        layout: {
            padding: {
                bottom: 20, // Adjust space below chart
            },
        },
    };

    return (
        <div>
            {/* Body */}
            <div className="d-flex">
                {/* Sidebar */}
                <div className="sidebar bg-dark text-white p-3" style={{ height: '100vh', width: '250px' }}>
                    <Nav className="flex-column">
                        <Nav.Link as={Link} to="/student-dashboard">Home</Nav.Link>
                        <Nav.Link as={Link} to="/leave">Apply Leave</Nav.Link>
                        <Nav.Link as={Link} to="/report">Leave Status</Nav.Link>
                        <Nav.Link as={Link} to="/profilest">Profile</Nav.Link>
                        <Nav.Link as={Link} to="/login">Logout</Nav.Link>
                    </Nav>
                </div>

                {/* Main Content */}
                <div className="flex-grow-1 p-4">
                    <Container fluid>
                        <Row>
                            <Col>
                                <div className="header mt-4 mb-4">
                                    <h1>Welcome, Student name</h1>
                                </div>
                            </Col>
                        </Row>
                        <Row>
                            <Col md={6}>
                                <div className="card">
                                    <div className="img">
                                        <Pie data={data} options={options} />
                                    </div>
                                    <div className="content">
                                        <div className="title">Total Requests:</div>
                                        <p><span className="color-indicator" style={{ backgroundColor: '#f3ff0a' }}></span>Pending request</p>
                                        <p><span className="color-indicator" style={{ backgroundColor: '#0fc502' }}></span>Approved request</p>
                                        <p><span className="color-indicator" style={{ backgroundColor: '#1900f4' }}></span>Rejected Request</p>
                                    </div>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </div>
            </div>
        </div>
    );
};

export default StudentDashboard;
